body {
  background: #fff;
  font-family: "roboto";
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.btn__blc {
  border-radius: 10px;
  background: #1b1a1a;
  padding: 11px 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  display: block;
  border: none;
  position: relative;
}
.btn__wht {
  border-radius: 10px;
  background: #fff;
  padding: 11px 22px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #1b1a1a;
  display: block;
  border: none;
  position: relative;
}
.btn__anim {
  display: block;
  position: relative;
}
.btn__anim b {
  display: block;
  position: relative;
  overflow: hidden;
}
.btn__anim i {
  display: block;
  width: 100%;
  position: relative;
}
.btn__anim i + i {
  position: absolute;
  left: 0;
  top: 100%;
}
.btn__anim span {
  display: inline-block;
  position: relative;
  transition: transform 0.6s cubic-bezier(0.76, 0, 0.24, 1);
}
.btn__anim:hover span {
  transform: translateY(-100%);
}

.input {
  position: relative;
}
.input p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.3s;
  transform-origin: 0% 50%;
  pointer-events: none;
}
.input input {
  background: transparent;
  padding: 0 0 5px;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, 0.7);
  box-shadow: none;
  color: #fff;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s;
}
.input input:focus, .input input:active, .input input:not(:placeholder-shown) {
  border-bottom: 1px solid #fff;
}
.input input:focus + p, .input input:active + p, .input input:not(:placeholder-shown) + p {
  transform: translateY(-100%) scale(0.8);
}
.input__check {
  position: relative;
  display: grid;
  grid-template-columns: 18px 1fr;
  gap: 10px;
}
.input__check p {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #fff;
}
.input__check p a {
  color: #6c63ff;
  position: relative;
  display: inline-block;
}
.input__check p a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #6c63ff;
  transition: all 0.3s;
}
.input__check p a::after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  height: 1px;
  background: #6c63ff;
  transition: all 0.3s;
  opacity: 1;
  left: 0;
  width: 100%;
  transition-delay: 0.1s;
}
.input__check p a:hover::after {
  right: 0;
  width: 0;
  left: auto;
  transition-delay: 0s;
}
.input__check p a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
  transition-delay: 0.1s;
}
.input__check span {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  border: 1px solid #fff;
  transition: all 0.3s;
}
.input__check span svg {
  opacity: 0;
  transition: all 0.3s;
}
.input__check input {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 100%;
  opacity: 0;
  cursor: pointer;
  z-index: 2;
}
.input__check input:checked + span {
  background: #fff;
}
.input__check input:checked + span svg {
  opacity: 1;
}

.nav {
  padding: 20px 0;
}
.nav__logo {
  max-width: 140px;
}
.nav__logo img {
  width: 100%;
}
.nav .container {
  display: flex;
  justify-content: start;
  align-items: center;
}
.nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 32px;
  margin: 0 32px 0 auto;
}
.nav ul a {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.6;
  color: #1b1a1a;
  transition: all 0.3s;
  position: relative;
}
.nav ul a::before {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  width: 0;
  right: 0;
  height: 1px;
  background: #1b1a1a;
  transition: all 0.3s;
}
.nav ul a:hover::before {
  left: 0;
  right: auto;
  width: 100%;
}

.header {
  padding: 120px 0;
  background: #ece7e4;
}
.header h1 {
  font-weight: 600;
  font-size: 64px;
  line-height: 1;
  color: #1b1a1a;
}
.header h1 span {
  color: #6c63ff;
}
.header__subtitle {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.1;
  color: #403e3b;
  margin: 30px 0 0;
  max-width: 500px;
}
.header .container {
  display: grid;
  grid-template-columns: 1fr 400px;
  gap: 30px;
}
.header__form {
  padding: 40px 20px;
  border-radius: 30px;
  background: #403e3b;
}
.header__form_text {
  font-weight: 400;
  font-size: 20px;
  line-height: 1.1;
  color: #fff;
}
.header__form .btn__wht {
  margin: 20px 0 0;
  width: 100%;
}
.header .input {
  margin: 30px 0 0;
}
.header .input__check {
  margin: 15px 0 0;
}